body {
  //font-family: 'Philosopher', sans-serif;
  padding: 20px;

  @media (max-width: $screen-sm) {
  	padding: 20px 0;
  }
}

#root h1 {
	@media (max-width: $screen-sm) {
		margin-bottom: 30px;
	}
}

a {
	color: #5995d8;
	text-decoration: none;
}

.navbar {
	padding: 0 0 20px;
	position: relative;
	text-align: center;

	.social {
		position: absolute;
		right: 0;
		top: 0;

		@media (max-width: $screen-sm) {
			position: relative;
		}
	}

	a {
		
	}
}

footer {
	padding: 20px 0 0;
}

.card-media {
	height: 150px;
}

.media {
	width: 1100px;

	@media (max-width: $screen-lg) {
		width: 800px;
	}

	@media (max-width: $screen-md) {
		width: 600px;
	}

	@media (max-width: $screen-sm) {
		width: 400px;
	}

	@media (max-width: $screen-xs) {
		width: 220px;
	}
}

.preview {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: contain;
	height: 200px;
}

.card-content {
  @media (min-width: $screen-sm) {
  	height: 100px;
  }
}
